	module.exports = {"data":{"markdownRemark":{"html":"<p>到华亭宾馆参加\"迈向二十一世纪国际大都市\"国际研讨会，这是针对二十一世纪上海的发展确立的研究课题，也是选择一种战略。一个国家和一个城市的发展，必须有明确的战略，选择战略，就是选择明智的发展道路。</p>\n<p>北京T教授在发言中，强调了\"以人的发展为中心的新战略\"，这是一个有高度、有文化的意见，基本的意见是：</p>\n<p>\"'经济是中心'，这个概念说明，经济不是孤立的、封闭的，它是开放的，不断改革的;它不但渗透到人类社会和自然界的各个领域，各个领域也渗透到经济中来。\"</p>\n<p>\"一个以人的发展为中心的社会经济战略，简要地概括起来，就是：人脑加电脑。人脑始终是第一位的。\"</p>\n<p>这是富有哲学意义的意见，也是符合人类社会发展潮流的意见。</p>\n<p>前几年在安曼举行过一次国际会议，会议向联合国提出一份报告，提出：过去四十年中，许多国家很少注意人的需要，人的忧虑，人的发展。报告建议九十年代的国际发展战略，应该以人的发展为中心，把重视人民生活作为第一发展战略，使人成为未来经济发展的主要动力和主要受益者。</p>\n<p>一般发展中国家在发展的初期，不是太重视人的发展，或者说是因为没有注意，或者说是没有资源来注意。在经济发展到一定阶段之后，必须确立这个发展战略。不然，经济最终也难以真正发展，就是发展了，人也就失落了。在经济发展水平比较低，社会资源比较紧张，人口比较多的社会中，明确这样一种意识，尤其重要。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0182.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0182.md","frontmatter":{"title":"11月11日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0182.md absPath of file >>> MarkdownRemark"}}

